<template>
  <div class="d-flex flex-column align-items-center">
    <div class="d-flex justify-content-around w-100 border-bottom pb-2">
      <h5>{{listing.name}}</h5>
      <h5>{{listing.bankName}}</h5>
      <h5>{{listing.bankAccountNumber}}</h5>
    </div>
    <hr>
    <h3 class="text-center">Available Budget in {{date}}:</h3>
    <h2 class="text-center">{{budget}}</h2>
    <div class="d-flex">
      <div class="d-flex justify-content-between bg-primary mr-3 p-2">
        <p class="text-white mb-0 mr-3">Income: </p>
        <p class="text-white mb-0">{{income}}</p>
      </div>
      <div class="d-flex justify-content-between bg-warning mr-3 p-2">
        <p class="text-white mb-0 mr-3">Expenses: </p>
        <p class="text-white mb-0">{{expenses}}</p>
      </div>
			<div class="d-flex justify-content-between bg-success p-2">
        <p class="text-white mb-0 mr-3">Planned this month: </p>
        <p class="text-white mb-0">{{plannedBudget}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import { monthNames } from '../../../utilities/months';

  export default {
    name: "BudgetHeader",
    data() {
      return {
        date: `${monthNames[new Date().getMonth()]} ${new Date().getFullYear()}`,
      }
    },
    computed: {
      budget() {
        return (this.income - this.expenses).toLocaleString();
      },
      income() {
        return this.$store.getters.getIncome;
      },
      expenses() {
        return this.$store.getters.getExpense;
      },
      listing() {
        return this.$store.getters.getListing(this.$route.params.id);
			},
			plannedBudgets() {
				return this.$store.getters.getUserPlannedBudgets;
			},
			plannedBudget() {
				let plannedBudgetSum = 0;
				this.plannedBudgets
					.filter(plannedBudget => new Date(plannedBudget.date).getMonth() === new Date().getMonth())
					.forEach(plannedBudget => {
						const subscription = plannedBudget.subscription;
						const value = plannedBudget.budget === '-' ? parseInt(plannedBudget.value) * (-1) : parseInt(plannedBudget.value);
						if(subscription === 'One time') {
							plannedBudgetSum += parseInt(value);
						} else if(subscription === 'Weekly') {
							plannedBudgetSum += parseInt(value) * 4;
						} else if(subscription === 'Daily') {
							plannedBudgetSum += parseInt(value) * 31;
						} else if(subscription === 'Monthly') {
							plannedBudgetSum += parseInt(value);
						} else if(subscription === 'Yearly') {
							plannedBudgetSum += parseFloat(value) / 12;
						}
					})
				return plannedBudgetSum;
				// return this.plannedBudgets.reduce((plannedBudgetSum, plannedBudget) => {
				// 	const plannedBudgetDate = new Date(plannedBudget.date);
				// 	const now = new Date();
				// 	if(plannedBudgetDate.getMonth() === now.getMonth()) {
				// 		switch (plannedBudget.subscription) {
				// 			case "One Time":
				// 				return plannedBudgetSum + parseInt(plannedBudget.value);
				// 				break;
				// 			default:
				// 				break;
				// 		}
				// 	}
				// }, 0);
				//return this.plannedBudgets.reduce((acc, pB) => acc + parseInt(pB.value), 0);
			}
    },
  }
</script>

