<template>
  <div>
    <input type="file" ref="fileInput" @change="processFile($event)">
    <table class="table table-hover mt-4" v-if="calcData.length">
      <thead>
        <tr>
          <th scope="col">ID</th>
          <th scope="col" v-for="(month, index) in months" :key="index">{{month}}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(year, index) in calcData" :key="index">
          <th scope="row">#{{index + 1}}</th>
          <td v-for="(month, index) in year" :key="index">{{month}}</td>
        </tr>
      </tbody>
    </table>
    <div class="row" v-if="calcData.length">
      <div class="col-lg-4" v-for="(year, index) in calcData" :key="index">
        <line-chart :labels="months" :series="[year]" :name="'year' + index"></line-chart>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-6">
        <button
          class="btn btn-primary"
          v-if="calcData.length && !avData.length"
          @click="calculatePlannedBudgets"
        >Calculate planned budgets</button>
        <div class="form-group mt-4 mb-2" v-if="avData.length">
          <label for="name">Planned Budgets Name</label>
          <input
            type="text"
            class="form-control"
            id="plannedbudgetsname"
            placeholder="Planned Budgets Name"
            v-model="plannedBudgetsName"
          >
        </div>
        <button
          class="btn btn-primary"
          v-if="avData.length"
          @click="makePlannedBudgets"
        >Make planned budgets</button>
      </div>
      <div class="col-lg-6" v-if="avData.length">
        <line-chart :labels="months" :series="[avData]" :name="'avData'"></line-chart>
      </div>
    </div>
  </div>
</template>

<script>
import LineChart from "../../components/UIElements/LineChart/LineChart.vue";

export default {
  name: "Calculator",
  components: {
    LineChart
  },
  data() {
    return {
      fileData: null,
      calculatorData: [],
      plannedBudgetsName: "",
      months: [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "June",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec"
      ]
    };
  },
  computed: {
    calcData() {
      return this.$store.getters.getCalcData;
    },
    avData() {
      return this.$store.getters.getAvaregeData;
    },
		listingId() {
			return this.$route.params.id;
		}
  },
  methods: {
    processFile(event) {
      this.fileData = event.target.files[0];
      const reader = new FileReader();
      reader.readAsText(this.fileData, "UTF-8");
      reader.onload = event => {
        const calculatorData = event.target.result
          .split("\n")
          .map(data => data.split(",").map(stringData => parseInt(stringData)));
        this.$store.dispatch("setCalcData", calculatorData);
      };
    },
    calculatePlannedBudgets() {
      let plannedData = new Array(12).fill(0, 0, 13);
      this.calcData.forEach(data => {
        data.forEach((monthlyData, index) => {
          plannedData[index] += monthlyData;
        });
      });
      let avaregeData = plannedData.map(data =>
        Math.ceil(data / this.calcData.length)
      );
      this.$store.dispatch("setAvaregeData", avaregeData);
    },
    makePlannedBudgets() {
      this.avData.forEach((data, index) => {
        const plannedBudget = {
          oneTimeOnly: "one",
          budget: "-",
          description: `${this.plannedBudgetsName} - ${this.months[index]}`,
          value: data,
          category: "Calculated data",
          date: new Date(`2018/${this.months[index]}`),
          subscription: "One time"
        };
        
        this.$store
          .dispatch("addPlannedBudget", {
            plannedBudget,
            listingId: this.listingId
          })
          .then(res => {
            //console.log(res);
          });
      });
    }
  }
};
</script>

