import axios from "axios";
import user from './user';

const state = {
  expenses: [],
  expensesLoaded: false
};

const mutations = {
  setExpenses(state, expenses) {
    state.expenses = expenses;
  },
  addExpense(state, expense) {
    state.expenses.push(expense);
  },
  editExpense(state, expense) {
    const index = state.expenses.findIndex(inc => inc.id === expense.id);
    state.expenses.splice(index, 1, expense);
  },
  deleteExpense(state, key) {
    const index = state.expenses.findIndex(exp => exp.key === key);
    state.expenses.splice(index, 1);
  },
  setExpensesLoaded(state, value) {
    state.expensesLoaded = value;
  }
};

const actions = {
  setExpenses({commit}, expenses) {
    commit('setExpenses', expenses);
  },
  fetchExpenses({commit}, listingId) {
    commit('setExpenses', []);
    return axios.get(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/expenses.json`)
      .then(res => {
        let expenseArr = [];
        for(const key in res.data) {
          expenseArr.push({...res.data[key], key: key});
        }
        commit('setExpenses', expenseArr);
        commit('setExpensesLoaded', true);
      })
  },
  addExpense({commit}, {expense, listingId}) {
    return axios.post(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/expenses.json`, expense)
      .then(res => {
        expense.key = res.data.name;
        commit('addExpense', expense);
      })
      .catch(err => console.error(err));
  },
  editExpense({commit}, {expense, listingId}) {
    axios.patch(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/expenses/${expense.key}.json`, expense)
      .then(res => commit('editExpense', expense))
      .catch(err => console.error(err));
  },
  deleteExpense({commit}, {key, listingId}) {
    axios.delete(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/expenses/${key}.json`)
      .then(res => commit('deleteExpense', key))
      .catch(err => console.error(err));
	},
	moveExpense({commit}, {expense, fromListing, toListing}) {
    axios.delete(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${fromListing}/expenses/${expense.key}.json`)
    .then(res => commit('deleteExpense', expense.key))
    .catch(err => console.error(err));
    axios.post(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${toListing}/expenses.json`, expense)
      .then(res => {
        console.log(res);
      })
      .catch(err => console.error(err));
  }
};

const getters = {
  getUserExpenses: state => state.expenses,
  getExpense(state) {
    if(!state.expenses) return 0;
    else {
      return state.expenses.reduce((accumulator, expense) => accumulator + parseInt(expense.value), 0);
    }
  },
  getUserExpense: state => keyString => state.expenses.find(expense => expense.key === keyString),
  getExpensesLoaded: state => state.expensesLoaded
};

export default {
  state,
  getters,
  mutations,
  actions
}