<template>
  <div class="row mb-5 border py-3">
    <div v-if="incomeCategoriesWithValue.length || expenseCategoriesWithValue.length" class="col-lg-12">
      <h3 class="text-center">Categories</h3>
    </div>
    <div v-else class="col-lg-12">
      <h4 class="text-center">No Category charts</h4>
    </div>
    <div v-if="incomeCategoriesWithValue.length" class="col-lg-4">
      <pie-chart name="income" :labels="incomeCategoriesWithValue" :series="incomeValues"></pie-chart>
      <h3 class="text-center">Income</h3>
    </div>
    <div v-if="expenseCategoriesWithValue.length" class="col-lg-4">
      <pie-chart name="expense" :labels="expenseCategoriesWithValue" :series="expenseValues"></pie-chart>
      <h4 class="text-center">Expenses</h4>
    </div>
    <div class="col-lg-4">
      <pie-chart name="inc-exp" :labels="['Income', 'Expenses']" :series="[incomesTotal, expensesTotal]"></pie-chart>
      <h4 class="text-center">Income / Expenses</h4>
    </div>
  </div>
</template>

<script>
import PieChart from "../../UIElements/PieChart/PieChart.vue";

export default {
  components: {
    PieChart
  },
  data() {
    return {
     
    };
  },
  computed: {
    incomes() {
      return this.$store.getters.getUserIncomes;
    },
    expenses() {
      return this.$store.getters.getUserExpenses;
    },
    categories() {
      return this.$store.getters.getUser.categories.map(
        category => category.label
      );
    },
    incomeCategoryValues() {
			const labels = this.categories.map(cat => ({ category: cat, value: 0 }));
			this.incomes.forEach(
      income =>
        (labels.find(cV => {
          return cV.category === income.category;
        }).value += parseInt(income.value))
			);
			return labels;
    },
    incomeCategoriesWithValue() {
      return this.incomeCategoryValues
        .filter(cV => cV.value !== 0)
        .map(catV => catV.category);
    },
    incomeValues() {
      return this.incomeCategoryValues
        .filter(cV => cV.value !== 0)
        .map(catV => catV.value);
    },
    expenseCategoryValues() {
			const labels = this.categories.map(cat => ({ category: cat, value: 0 }));
			this.expenses.forEach(
      expense =>
        (labels.find(cV => {
          return cV.category === expense.category;
        }).value += parseInt(expense.value))
			);
			return labels;
    },
    expenseCategoriesWithValue() {
      return this.expenseCategoryValues
        .filter(cV => cV.value !== 0)
        .map(catV => catV.category);
    },
    expenseValues() {
      return this.expenseCategoryValues
        .filter(cV => cV.value !== 0)
        .map(catV => catV.value);
    },
    incomesTotal() {
      return this.incomes.reduce((acc, inc) => acc += parseInt(inc.value), 0);
    },
    expensesTotal() {
      return this.expenses.reduce((acc, exp) => acc += parseInt(exp.value), 0);
    },
  }
};
</script>

