<template>
	<div class="row py-5 border">
		<div class="d-flex ml-3">
			<div class="d-flex align-items-center mr-2">
				<div class="chart-marker chart-income mr-1"></div>
				Income
			</div>
			<div class="d-flex align-items-center">
				<div class="chart-marker chart-expense mr-1"></div>
				Expenses
			</div>
		</div>
		<div class="col-lg-12">
			<h3>Current Year - {{new Date().getFullYear()}}</h3>
			<line-chart name="year" :labels="months" :series="[incomesMonthlyValues, expensesMonthlyValues]"></line-chart>
		</div>
		<div class="col-lg-12 mt-2">
			<h3>Current Month - {{months[new Date().getMonth()]}}</h3>
			<line-chart name="days" :labels="days" :series="[incomesDailyValues, expensesDailyValues]"></line-chart>
		</div>
		<week-chart class="mt-2"></week-chart>
	</div>
</template>

<script>
	import LineChart from '../../UIElements/LineChart/LineChart.vue';
	import WeekChart from '../WeekChart/WeekChart.vue';

	export default {
		components: {
			LineChart,
			WeekChart
		},
		mounted() {
			console.log(this.series);
		},
		data() {
			return {
				months: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'],
				series: [],
			}
		},
		computed: {
			days() {
				const ds = [];
				for(let i = 1; i < 32; i++) {
					ds.push(i.toString());
				}
				return ds;
			},
			incomes() {
				return this.$store.getters.getUserIncomes;
			},
			expenses() {
				return this.$store.getters.getUserExpenses;
			},
			incomesMonthlyValues() {
				const monthlyValues = new Array(12).fill(0, 0, 13);
				this.incomes.forEach(inc => {
					const month = new Date(inc.date).getMonth();
					monthlyValues[month] += parseInt(inc.value);
				});
				return monthlyValues;
			},
			expensesMonthlyValues() {
				const monthlyValues = new Array(12).fill(0,0,13);
				this.expenses.forEach(exp => {
					const month = new Date(exp.date).getMonth();
					monthlyValues[month] += parseInt(exp.value);
				});
				return monthlyValues;
			},
			incomesDailyValues() {
				const dailyValues = new Array(31).fill(0,0,32);
				this.incomes.forEach(inc => {
					const incDate = new Date(inc.date);
					const now = new Date();
					if(incDate.getMonth() === now.getMonth() && incDate.getFullYear() === now.getFullYear()) {
						const day = incDate.getUTCDate();
						dailyValues[day - 1] += parseInt(inc.value);
					}
				})
				return dailyValues;
			},
			expensesDailyValues() {
				const dailyValues = new Array(31).fill(0,0,32);
				this.expenses.forEach(exp => {
					const expDate = new Date(exp.date);
					const now = new Date();
					if(expDate.getMonth() === now.getMonth() && expDate.getFullYear() === now.getFullYear()) {
						const day = expDate.getUTCDate();
						dailyValues[day - 1] += parseInt(exp.value);
					}
				})
				return dailyValues;
			}
		},
	}
</script>

<style scoped>
	.chart-marker {
		width: 10px;
		height: 10px;
	}

	.chart-income {
		background-color: #0E4D92;
	}

	.chart-expense {
		background-color: #0080FF;
	}
</style>