import Vue from 'vue'
import Router from 'vue-router'
import LoginPage from './views/LoginPage';
import DashboardPage from './views/DashboardPage';
import Budget from './components/Budget/Budget.vue';
import AccountSettings from './components/AccountSettings/AccountSettings.vue';
import Listings from './components/Listings/Listings.vue';
import Charts from './components/Charts/Charts.vue';
import store from './store/store';
import BudgetList from './components/Budget/BudgetList/BudgetList.vue';
import PlannedBudgetList from './components/PlannedBudget/PlannedBudgetList/PlannedBudgetList.vue';
import GoalList from './components/Goals/GoalList/GoalList.vue';
import Calculator from './components/Calculator/Calculator.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: LoginPage
    },
    {
      path: '/dashboard', name: 'dashboard', component: DashboardPage, redirect: '/dashboard/listings',
      beforeEnter(to, from, next) {
        if(store.state.auth.idToken) {
          next()
        } else {
          next('/')
        }
      },
      children: [
        {path: 'listings', component: Listings},
        {path: 'listings/:id', component: Budget, redirect: 'listings/:id/budget', children: [
          {path: 'budget', component: BudgetList},
          {path: 'planned-budgets', component: PlannedBudgetList},
          {path: 'goals', component: GoalList},
					{path: 'charts', component: Charts},
					{path: 'calculator', component: Calculator}
        ]},
        {path: 'account-settings', component: AccountSettings},
      ]
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
