<template>
	<div :class="name">
		
	</div>
</template>

<script>
import Chartist from "chartist";

export default {
  props: {
    labels: {
      type: Array,
      default: () => []
    },
    series: {
      type: Array,
      default: () => []
    },
    name: {
      type: String,
      default: ''
    }
  },
  mounted() {
    new Chartist.Bar(`.${this.name}`, {
      labels: this.labels,
      series: this.series
    }, {
      height: '250px'
    });
  },
  data() {
    return {
    };
  }
};
</script>

<style lang="scss">
  .ct-chart-bar .ct-label {
    color: #333 !important;
    fill : #333 !important;
  }

  .ct-chart-bar {
    height: 800px;
  }
</style>