<template>
  <div class="bg-white p-3">
    <div class="d-flex justify-content-between">
      <h5 class="m-0">Add Budget</h5>
      <span class="close" @click="$emit('close')"><i class="material-icons">clear</i></span>
    </div>

    <hr>
    <form class="budget-control">
      <datepicker class="date mb-3" wrapper-class="datepicker" input-class="w-100 px-2" placeholder="Date"
                  v-model="budget.date"></datepicker>
      <select class="expense custom-select mb-3" id="expense" v-model="budget.amplitude">
        <option>-</option>
        <option>+</option>
      </select>
      <input type="text" class="desc form-control mb-3" placeholder="Add Description" v-model="budget.description">
      <input type="number" min="0" class="value form-control mb-3" placeholder="Value" v-model="budget.value">
      <select class="category custom-select mb-3" id="category" v-if="categories.length" v-model="budget.category">
        <option disabled>Categories</option>
        <option v-for="(category, index) in categories" :key="index">{{category.label}}</option>
      </select>
      <select class="category custom-select mb-3" id="listings" v-if="keyString" v-model="listing">
        <option disabled>Listings</option>
        <option v-for="(listing, index) in listings" :key="index">{{listing.name}}</option>
      </select>
      <button class="btn btn-block" :class="buttonColor" @click="add" type="button">{{buttonText}}</button>
    </form>
  </div>

</template>

<script>
  import Datepicker from 'vuejs-datepicker';
  import { validate } from "../../../utilities/validation";

  export default {
    name: "BudgetControl",
    components: {
      Datepicker
    },
    props: {
      keyString: {
        type: String,
        default: ''
      },
      budgetType: {
        type: String,
        default: ''
      }
    },
    created() {
      if(this.keyString) {
        if(this.budgetType === 'income') {
          this.budget = {...this.$store.getters.getUserIncome(this.keyString)};
          this.budget.amplitude = '+';
        } else {
          this.budget = {...this.$store.getters.getUserExpense(this.keyString)};
          this.budget.amplitude = '-';
        }
        this.listing = this.originalListing.name;
      }
    },
    computed: {
      categories() {
        return this.$store.getters.getCategories;
      },
      buttonText() {
        const isEditString = this.keyString ? 'Edit' : 'Add';
        return this.budget.amplitude === '-' ? `${isEditString} Expense` : `${isEditString} Income`;
      },
      buttonColor() {
        return this.budget.amplitude === '-' ? 'btn-warning' : 'btn-primary';
      },
      listingId() {
        return this.$route.params.id;
      },
      listings() {
        return this.$store.getters.getListings;
      },
      selectedListing() {
        return this.listings.find(listing => listing.name === this.listing);
      },
      originalListing() {
        return this.$store.getters.getListing(this.listingId);
      },
      modifiedListing() {
				if(this.keyString)
					return this.originalListing.name !== this.listing;
				return false;
      }
    },
    data() {
      return {
        budget: {
          amplitude: '-',
          description: '',
          value: '',
          category: 'Categories',
          date: new Date(),
        },
        descriptionError: {
          descriptionMsg: '',
          error: false
        },
        valueError: {
          valueMsg: '',
          error: false
        },
        categoryError: {
          categoryMsg: '',
          error: false
        },
        dateError: {
          dateMsg: '',
          error: false
        },
        listing: ''
      }
    },
    methods: {
      add() {
        this.descriptionError = validate(this.budget.description, 'text');
        this.valueError = validate(this.budget.value, 'number');
        if(!this.descriptionError.error && !this.valueError.error && this.budget.category !== 'Categories') {
          if(this.budget.amplitude === '+') {
            if(this.keyString && !this.modifiedListing) {
              this.$store.dispatch('editIncome', {income: this.budget, listingId: this.listingId})
                .then(res => {
                  this.$emit('close');
                });
            } else if(!this.modifiedListing) {
              this.$store.dispatch('addIncome', {income: this.budget, listingId: this.listingId})
                .then(res => {
                  this.$emit('close');
                })
            } else {
              this.$store.dispatch('moveIncome', {
                income: this.budget,
                fromListing: this.listingId,
                toListing: this.selectedListing.key
              }).then(res => {
                  this.$emit('close');
              });
            }
          } else {
            if(this.keyString && !this.modifiedListing) {
              this.$store.dispatch('editExpense', {expense: this.budget, listingId: this.listingId})
                .then(res => this.$emit('close'))
            } else if(!this.modifiedListing) {
              this.$store.dispatch('addExpense', {expense: this.budget, listingId: this.listingId})
                .then(res => {
                  this.$emit('close');
                })
            } else {
              this.$store.dispatch('moveExpense', {
                expense: this.budget,
                fromListing: this.listingId,
                toListing: this.selectedListing.key
              }).then(res => {
                  this.$emit('close');
              });
            }

          }
        }
      }
    },
  }
</script>

<style lang="scss">
  .budget-control {

  }

  .add {
    background-color: #e2e6ea;
  }

  .datepicker {
    div:first-child {
      height: 100%;
      display: flex;
    }

    input {
      border: 1px solid #ced4da;
      padding: 6px 12px;

      &::placeholder {
        color: #7e868e;
      }
    }
  }

  .close {
    cursor: pointer;
  }
</style>