import axios from "axios";
import user from './user';

const state = {
  incomes: [],
  incomesLoaded: false
};

const mutations = {
  setIncomes(state, incomes) {
    state.incomes = incomes;
  },
  addIncome(state, income) {
    state.incomes.push(income);
  },
  editIncome(state, income) {
    const index = state.incomes.findIndex(inc => inc.key === income.key);
    state.incomes.splice(index, 1, income);
  },
  deleteIncome(state, key) {
    const index = state.incomes.findIndex(inc => inc.key === key);
    state.incomes.splice(index, 1);
  },
  setIncomesLoaded(state, value) {
    state.incomesLoaded = value;
  }
};

const actions = {
  setIncomes({commit}, incomes) {
    commit('setIncomes', incomes);
  },
  fetchIncomes({commit}, listingId) {
    commit('setIncomes', []);
    return axios.get(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/incomes.json`)
      .then(res => {
        let incomeArr = [];
        for(const key in res.data) {
          incomeArr.push({...res.data[key], key: key});
        }
        commit('setIncomes', incomeArr);
        commit('setIncomesLoaded', true);
      })
  },
  addIncome({commit}, {income, listingId}) {
    return axios.post(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/incomes.json`, income)
      .then(res => {
        income.key = res.data.name;
        commit('addIncome', income);
      })
      .catch(err => console.error(err));
  },
  editIncome({commit}, {income, listingId}) {
    axios.patch(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/incomes/${income.key}.json`, income)
      .then(res => commit('editIncome', income))
      .catch(err => console.error(err));
  },
  deleteIncome({commit}, {key, listingId}) {
    axios.delete(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/incomes/${key}.json`)
      .then(res => commit('deleteIncome', key))
      .catch(err => console.error(err));
  },
  moveIncome({commit}, {income, fromListing, toListing}) {
    axios.delete(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${fromListing}/incomes/${income.key}.json`)
    .then(res => commit('deleteIncome', income.key))
    .catch(err => console.error(err));
    axios.post(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${toListing}/incomes.json`, income)
      .then(res => {
        console.log(res);
      })
      .catch(err => console.error(err));
  }
};

const getters = {
  getUserIncomes: state => state.incomes,
  getIncome(state) {
    if(!state.incomes) return 0;
    else {
      return state.incomes.reduce((accumulator, income) => accumulator + parseInt(income.value), 0);
    }
  },
  getUserIncome: state => keyString => state.incomes.find(income => income.key === keyString),
  getIncomesLoaded: state => state.incomesLoaded
};

export default {
  state,
  getters,
  mutations,
  actions
}