import axios from 'axios';

const state = {
	calcData: [],
	avaregeData: []
};

const mutations = {
	setCalcData(state, calcData) {
		state.calcData = calcData;
	},
	setAvaregeData(state, avData) {
		state.avaregeData = avData;
	}
};

const actions = {
  setCalcData({commit}, calcData) {
		commit('setCalcData', calcData);
	},
	setAvaregeData({commit}, avData) {
		commit('setAvaregeData', avData);
	}
};

const getters = {
	getCalcData: state => state.calcData,
	getAvaregeData: state => state.avaregeData
};

export default {
  state,
  mutations,
  actions,
  getters
}