import axios from "axios";
import user from "./user";

const state = {
  plannedBudgets: [],
  plannedBudgetsLoaded: false
};

const mutations = {
  setPlannedBudgets(state, plannedBudgets) {
    state.plannedBudgets = plannedBudgets;
  },
  addPlannedBudget(state, plannedBudget) {
    state.plannedBudgets.push(plannedBudget);
  },
  editPlannedBudget(state, plannedBudget) {
    const index = state.plannedBudgets.findIndex(
      inc => inc.key === plannedBudget.key
    );
    state.plannedBudgets.splice(index, 1, plannedBudget);
  },
  deletePlannedBudget(state, key) {
    const index = state.plannedBudgets.findIndex(
      inc => inc.key === key
		);
    state.plannedBudgets.splice(index, 1);
  },
  setPlannedBudgetsLoaded(state, value) {
    state.plannedBudgetsLoaded = value;
  }
};

const actions = {
  setPlannedBudgets({ commit }, plannedBudgets) {
    commit("setPlannedBudgets", plannedBudgets);
  },
  async fetchPlannedBudgets({commit}, listingId) {
    commit("setPlannedBudgets", []);
    const plannedBudgetsResponse = await axios.get(
      `https://cashwise-a8d6a.firebaseio.com/users/${
        user.state.user.key
      }/listings/${listingId}/plannedBudgets.json`
    );
		const plannedBudgets = plannedBudgetsResponse.data;
    let plannedBudgetsArr = [];
    for (const key in plannedBudgets) {
      plannedBudgetsArr.push({ ...plannedBudgets[key], key: key });
    }
    commit("setPlannedBudgets", plannedBudgetsArr);
    commit('setPlannedBudgetsLoaded', true);
  },
  async addPlannedBudget({ commit }, {plannedBudget, listingId}) {
    try {
      const res = await axios
        .post(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/plannedBudgets.json`, plannedBudget);
      plannedBudget.key = res.data.name;
      commit("addPlannedBudget", plannedBudget);
    }
    catch (err) {
      return console.error(err);
    }
  },
  editPlannedBudget({ commit }, {plannedBudget, listingId}) {
    axios
      .patch(
        `https://cashwise-a8d6a.firebaseio.com/users/${
          user.state.user.key
        }/listings/${listingId}/plannedBudgets/${plannedBudget.key}.json`,
        plannedBudget
      )
      .then(res => commit("editPlannedBudget", plannedBudget))
      .catch(err => console.error(err));
  },
  deletePlannedBudget({ commit }, {key, listingId}) {
    axios
      .delete(
        `https://cashwise-a8d6a.firebaseio.com/users/${
          user.state.user.key
        }/listings/${listingId}/plannedBudgets/${key}.json`
      )
      .then(res => commit("deletePlannedBudget", key))
      .catch(err => console.error(err));
  }
};

const getters = {
  getUserPlannedBudgets: state => state.plannedBudgets,
  getUserPlannedBudget: state => keyString => state.plannedBudgets.find(pb => pb.key === keyString),
  getPlannedBudgetsLoaded: state => state.plannedBudgetsLoaded
};

export default {
  state,
  mutations,
  actions,
  getters
};
