<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <div class="bg-white shadow my-5 p-3">
          <h3 class="text-dark text-center mb-5">Choose a Listing</h3>
          <div v-for="(listing, index) in listings"
               :key="index"
               class="listing p-2 mb-4"
               @click="goToListing(listing.key)">
            <h5 class="mb-0">{{listing.name}}</h5>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Listings",
    created() {
      this.$store.commit('setIncomesLoaded', false);
      this.$store.commit('setExpensesLoaded', false);
      this.$store.commit('setPlannedBudgetsLoaded', false);
			this.$store.commit('setGoalsLoaded', false);
			this.$store.commit("setPlannedBudgets", []);
			this.$store.commit("setGoals", []);
    },
    computed: {
      listings() {
        return this.$store.getters.getListings;
      }
    },
    methods: {
      goToListing(key) {
        this.$router.push(`/dashboard/listings/${key}`);
      }
    },
  }
</script>

<style scoped lang="scss">
  .listing {
    background-color: white;
    cursor: pointer;
    color: #2780E3;
    border: 1px solid #2780E3;
    transition: all .2s ease;

    &:hover {
     color: white;
     background-color: #2780E3;
    }
  }
</style>