<template>
  <div class="mt-5">
    <h4 class="text-center text-primary mb-3">Planned Budgets</h4>
		<select class="category custom-select mb-3" id="category" v-if="months.length" v-model="selectedMonth">
        <option>Month</option>
        <option v-for="(month, index) in months" :key="index">{{month}}</option>
      </select>
    <table v-if="plannedBudgets.length" class="table table-hover">
      <thead>
        <tr class="text-white bg-primary">
          <td>Budget</td>
          <td>Description</td>
          <td>Date</td>
          <td>Subscription</td>
          <td>Value</td>
          <td></td>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(plannedBudget, index) in plannedBudgets"
          :key="index"
          class="plannedBudgets-row"
        >
          <td>{{plannedBudget.budget === '-' ? 'Expense' : 'Income'}}</td>
          <td>{{plannedBudget.description}}</td>
          <td>{{formatDate(plannedBudget.date)}}</td>
          <td v-if="plannedBudget.subscription">{{plannedBudget.subscription}}</td>
          <td v-else>-</td>
          <td>{{plannedBudget.budget}}{{plannedBudget.value}}</td>
          <td class="plannedBudgets-row__delete">
            <span class="text-primary mr-2" @click="editPlannedBudget(plannedBudget.key)">Edit</span>
            <span class="text-danger" @click="deletePlannedBudget(plannedBudget.key)">Delete</span>
          </td>
        </tr>
      </tbody>
    </table>
    <div v-else>
      <h4 class="text-center">You dont'have planned budgets yet</h4>
    </div>
    <confirm-modal
      :show="confirmModalShow"
      @close="openConfirmModal"
      title="Do you want to delete this planned budget?"
      cancel-text="Cancel"
      confirm-text="Delete"
      text="This planned budget will be deleted from your account"
      @confirm="confirmDelete"
    ></confirm-modal>
    <planned-budget-modal
      :show="plannedBudgetModalShow"
      :keyString="selectedPlannedBudget"
      @close="openPlannedBudgetModal"
    ></planned-budget-modal>
  </div>
</template>

<script>
import moment from "moment";
import PlannedBudgetModal from "../PlannedBudgetModal/PlannedBudgetModal";
import ConfirmModal from "../../UIElements/ConfirmModal/ConfirmModal";

export default {
  name: "PlannedBudgetList",
  components: { ConfirmModal, PlannedBudgetModal },
  created() {
    if (!this.plannedBudgetsLoaded) {
      this.$store.dispatch("fetchPlannedBudgets", this.$route.params.id);
    }
  },
  data() {
    return {
      plannedBudgetModalShow: false,
      selectedPlannedBudget: "",
      confirmModalShow: false,
			months: [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "June",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec"
      ],
			selectedMonth: 'Month'
    };
  },
  computed: {
    listingId() {
      return this.$route.params.id;
		},
		plannedBudgets() {
			if(this.selectedMonth === 'Month') {
				return this.$store.getters.getUserPlannedBudgets;
			}
			return this.$store.getters.getUserPlannedBudgets.filter(pb => {
				const selectedMonthId = this.months.findIndex(month => month === this.selectedMonth);
				const pbMonth = new Date(pb.date).getMonth()
				return selectedMonthId === pbMonth;
			})
		},
		plannedBudgetsLoaded() {
			return this.$store.getters.getPlannedBudgetsLoaded;
		}
  },
  methods: {
    formatDate(date) {
      return moment(date).format("YYYY/MM/DD");
    },
    editPlannedBudget(key) {
      this.plannedBudgetModalShow = true;
      this.selectedPlannedBudget = key;
    },
    openPlannedBudgetModal() {
      this.plannedBudgetModalShow = !this.plannedBudgetModalShow;
    },
    deletePlannedBudget(key) {
      this.confirmModalShow = true;
      this.selectedPlannedBudget = key;
    },
    openConfirmModal() {
      this.confirmModalShow = !this.confirmModalShow;
    },
    confirmDelete() {
      this.$store.dispatch("deletePlannedBudget", {
        key: this.selectedPlannedBudget,
        listingId: this.listingId
			});
			console.log(this.selectedPlannedBudget);
      this.openConfirmModal();
    }
  }
};
</script>

<style scoped lang="scss">
.plannedBudgets-row {
  &__delete {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
  }

  &:hover &__delete {
    opacity: 1;
    transform: translateX(-2px);
  }
}
</style>