<template>
	<div class="col-lg-12">
		<h3>Current Week</h3>
		<bar-chart name="week" :labels="days" :series="[incomesDailyValues, expensesDailyValues]"></bar-chart>
	</div>
</template>

<script>
import BarChart from "../../UIElements/BarChart/BarChart.vue";

export default {
  components: {
    BarChart
  },
  data() {
    return {
      days: ["Mon", "Tue", "Wed", "Thur", "Fri", "Sat", "Sun"]
    };
  },
  computed: {
    incomes() {
      return this.$store.getters.getUserIncomes;
    },
    expenses() {
      return this.$store.getters.getUserExpenses;
    },
    test() {
      return this.getWeekNumber(new Date(this.incomes[0].date));
    },
    incomesDailyValues() {
      const dailyValues = new Array(7).fill(0, 0, 7);
      this.incomes.forEach(inc => {
        const incDate = new Date(inc.date);
        const now = new Date();
        if (this.getWeekNumber(incDate) === this.getWeekNumber(now)) {
          const day = incDate.getDay();
          if (day === 0) {
            dailyValues[6] += parseInt(inc.value);
          } else {
            dailyValues[day - 1] += parseInt(inc.value);
          }
        }
      });
      return dailyValues;
    },
    expensesDailyValues() {
      const dailyValues = new Array(7).fill(0, 0, 7);
      this.expenses.forEach(exp => {
        const expDate = new Date(exp.date);
        const now = new Date();
        if (this.getWeekNumber(expDate) === this.getWeekNumber(now)) {
          const day = expDate.getDay();
          if (day === 0) {
            dailyValues[6] += parseInt(exp.value);
          } else {
            dailyValues[day - 1] += parseInt(exp.value);
          }
        }
      });
      return dailyValues;
    }
  },
  methods: {
    getWeekNumber(d) {
      // Copy date so don't modify original
      d = new Date(Date.UTC(d.getFullYear(), d.getMonth(), d.getDate()));
      // Set to nearest Thursday: current date + 4 - current day number
      // Make Sunday's day number 7
      d.setUTCDate(d.getUTCDate() + 4 - (d.getUTCDay() || 7));
      // Get first day of year
      var yearStart = new Date(Date.UTC(d.getUTCFullYear(), 0, 1));
      // Calculate full weeks to nearest Thursday
      var weekNo = Math.ceil(((d - yearStart) / 86400000 + 1) / 7);
      // Return array of year and week number
      return weekNo;
    }
  }
};
</script>

