<template>
	<div :class="name">
		
	</div>
</template>

<script>
import Chartist from "chartist";

export default {
  props: {
    labels: {
      type: Array,
      default: []
    },
    series: {
      type: Array,
      default: []
    },
    name: {
      type: String,
      required: true
    }
  },
  mounted() {
    new Chartist.Pie(`.${this.name}`, {
      labels: this.labels,
      series: this.series
    });
  },
  data() {
    return {
      
    };
  }
};
</script>

