import axios from "axios";
import user from './user';

const state = {
  goals: [],
  goalsLoaded: false
};

const mutations = {
  setGoals(state, goals) {
    state.goals = goals;
  },
  addGoal(state, goal) {
    state.goals.push(goal);
  },
  editGoal(state, goal) {
    const index = state.goals.findIndex(inc => inc.key === goal.key);
    state.goals.splice(index, 1, goal);
  },
  deleteGoal(state, key) {
    const index = state.goals.findIndex(inc => inc.key === key);
    state.goals.splice(index, 1);
  },
  setGoalsLoaded(state, value) {
    state.goalsLoaded = value;
  }
};

const actions = {
  setGoals({commit}, goals) {
    commit('setGoals', goals);
  },
  async fetchGoals({commit}, listingId) {
    commit('setGoals', []);
    const res = await axios.get(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/goals.json`);
    let goalsArr = [];
    for (const key in res.data) {
      goalsArr.push({ ...res.data[key], key: key });
    }
    commit('setGoals', goalsArr);
    commit('setGoalsLoaded', true);
  },
  async addGoal({commit}, {goal, listingId}) {
    try {
      const res = await axios.post(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/goals.json`, goal);
      goal.key = res.data.name;
      commit('addGoal', goal);
    }
    catch (err) {
      return console.error(err);
    }
  },
  editGoal({commit}, {goal, listingId}) {
    axios.patch(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/goals/${goal.key}.json`, goal)
      .then(res => commit('editGoal', goal))
      .catch(err => console.error(err));
  },
  deleteGoal({commit}, {key, listingId}) {
    axios.delete(`https://cashwise-a8d6a.firebaseio.com/users/${user.state.user.key}/listings/${listingId}/goals/${key}.json`)
      .then(res => commit('deleteGoal', key))
      .catch(err => console.error(err));
  }
};

const getters = {
  getUserGoals: state => state.goals,
  getUserGoal: state => keyString => state.goals.find(goal => goal.key === keyString),
  getGoalsLoaded: state => state.goalsLoaded
};

export default {
  state,
  getters,
  mutations,
  actions
}